import React, { useState } from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail'
import { connect } from 'react-redux'
import { UI_CLOSE_SIDEBAR } from '../../redux/actionTypes'
import MenuIcon from '@material-ui/icons/Menu';
import routes from '../routes'
import {
    Link
  } from "react-router-dom";

const useStyles = makeStyles({
    list: {
        width: 250,
        '& a': {
            color: 'inherit',
            textDecoration: 'none'
        }
    },
    item: {
        borderBottom: '1px solid #ccc'
    },
    toggler: {
        position: 'fixed',
        top: 12,
        left: 12,
        zIndex: 99
    }
});

const Sidebar = props => {
    const classes = useStyles();
    // const { isOpen, handleClose } = props
    const [ isOpen, setIsOpen ] = useState(false)
    const handleClose = () => setIsOpen(false)
    const handleOpen = () => setIsOpen(true)

    return (
        <>
        <MenuIcon onClick={handleOpen} className={classes.toggler} />
        <Drawer open={isOpen} onClose={handleClose}>
            <div
                className={classes.list}
                role="presentation"
                onClick={handleClose}
                onKeyDown={handleClose}
            >
                <List>
                    {routes.filter(r => r.displayName).map(r => (
                        <Link to={r.path} key={r.path}>
                            <ListItem button className={classes.item}>
                                {/* <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon> */}
                                <ListItemText primary={r.displayName} />
                            </ListItem>
                        </Link>
                    ))}
                </List>
            </div>
        </Drawer>
        </>
    );
}
// const mapStateTtoProps = state => ({
//     isOpen: state.ui.sidebarOpen
// })
// const mapDispatchToProps = dispatch => ({
//     handleClose: () => dispatch({ type: UI_CLOSE_SIDEBAR })
// })
// export default connect(mapStateTtoProps, mapDispatchToProps)(Sidebar)
export default Sidebar
