import React, { useState, useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import { URL_MENU, SUB_ROOT } from '../../../utils/urls'
import { Box, Typography } from '@material-ui/core'
import { PageHeader } from '../../common'
import LANG from '../../../utils/lang'

const useStyles = makeStyles({
    menu: {
        listStyle: 'none',
        textAlign: "center",
        marginTop: 15,
        '& li': {
            margintTop: 10,
            borderBottom: '1px dotted #ccc'
        },
        '& li a': {
            color: 'inherit',
            textDecoration: 'none',
            display: 'block',
            padding: '5px 15px'
        }
    }
});

const Menu = props => {
    const [menu, setMenu] = useState([])
    const classes = useStyles()
    useEffect(() => {
        fetch(URL_MENU)
        .then(r => r.json())
        .then(r => {
            // console.log("menu = ", r)
            setMenu(r)
        })
        .catch(err => console.log(err))
    }, [])
    return (
        <Box my={3}>
            <PageHeader txt={LANG.MENU} />
            <Box>
                <ul className={classes.menu}>
                    {
                        menu.map( (m,i) => 
                        <li key={i}>
                            <a href={`#/read/${m.id}/${encodeURIComponent(m.name)}`}>{m.name}</a>
                        </li>
                        )
                    }
                </ul>
            </Box>
        </Box>
    )
}

export default Menu