import React, { useState, useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import { URL_MENU, SUB_ROOT } from '../../../utils/urls'
import { Box, Switch, Select, MenuItem } from '@material-ui/core'
import { PageHeader } from '../../common'
import LANG from '../../../utils/lang'
import { SETTINGS_SAVE } from '../../../redux/actionTypes'
import { connect } from 'react-redux'

const useStyles = makeStyles({
    item: {
        display: 'flex',
        alignItems: 'center',
        // marginTop: 10,
        padding: '15px 5px 5px 5px',
        borderBottom: '1px solid #ccc',
        '& .name': {
            flexGrow: 1,
            fontSize: 18,
        }
    }
});

const Settings = props => {
    // const initSettings = {
    //     isNightMode: false,
    //     fontSize: 16,
    //     readerPref: 'PAGE'
    // }
    // const [settings, setSettings] = useState({...initSettings})
    const { settings, saveSettings } = props

    const handleChange = e => {
        let k = e.target.name
        let v = e.target.value
        if(k === 'isNightMode') {
            v = !settings.isNightMode
            let nightModeClass = 'nightmode'
            let classes = document.body.className.split(' ')
            if(v) {
                if(!classes.includes(nightModeClass)) {
                    classes.push(nightModeClass)
                }
            }
            else {
                if(classes.includes(nightModeClass)) {
                    classes.splice(classes.indexOf(nightModeClass), 1)
                }
            }
            document.body.className = classes.join(' ').trim()
        }
        saveSettings({
            ...settings,
            [k]: v
        })
    }

    const classes = useStyles()

    return (
        <Box my={3}>
            <PageHeader txt={LANG.SETTINGS} />
            <Box>
                <div className={classes.item}>
                    <div className="name">Night Mode</div>
                    <div className="cntrl">
                        Off
                        <Switch
                            checked={settings.isNightMode}
                            onChange={handleChange}
                            name="isNightMode"
                            color="default"
                            inputProps={{ 'aria-label': 'secondary checkbox' }}
                        />
                        On
                    </div>
                </div>
                <div className={classes.item}>
                    <div className="name">Font Size</div>
                    <div className="cntrl">
                        <Select
                            name="fontSize"
                            value={settings.fontSize}
                            onChange={handleChange}
                        >
                            <MenuItem value={14}>14</MenuItem>
                            <MenuItem value={16}>16</MenuItem>
                            <MenuItem value={18}>18</MenuItem>
                        </Select>
                    </div>
                </div>
                <div className={classes.item}>
                    <div className="name">Reader View Preference</div>
                    <div className="cntrl">
                        <Select
                            name="readerPref"
                            value={settings.readerPref}
                            onChange={handleChange}
                        >
                            <MenuItem value={'SCROLL'}>Scroll</MenuItem>
                            <MenuItem value={'PAGE'}>Page</MenuItem>
                        </Select>
                    </div>
                </div>
            </Box>
        </Box>
    )
}

const mapStateToProps = state => ({
    settings: state.settings
})
const mapDispatchToProps = dispatch => ({
    saveSettings: s => dispatch({ type: SETTINGS_SAVE, payload: s })
})
export default connect(mapStateToProps, mapDispatchToProps)(Settings)