import React, { useState, useEffect } from 'react'
import clsx from 'clsx'
import { makeStyles } from '@material-ui/core/styles';
import { URL_READ_STORY } from '../../../utils/urls'
import { Box, Typography } from '@material-ui/core'
// import LANG from '../../../utils/lang'
import RightIcon from '@material-ui/icons/KeyboardArrowRight'
import LeftIcon from '@material-ui/icons/KeyboardArrowLeft'
import { connect } from 'react-redux'

const readableHeight = window.innerHeight - 80
let leavingPage = false

const useStyles = makeStyles({
    storyTitle: {
        fontSize: 18,
        textAlign: 'center',
        borderBottom: '1px solid #ccc',
        marginTop: 10
    },
    body: {
        // color: 'rgba(0, 0, 0, .9)',
        height: readableHeight,
        display: 'flex',
        textAlign: 'justify',
        // alignItems: 'center',
        zIndex: 9,
        opacity: .8
        // background: '#fff'
    },
    // hiddenhelper: {
    //     border: '1px solid #ccc',
    //     height: 'auto',
    //     width: 'auto',
    //     whiteSpace: 'pre-wrap'
    // },
    pageCount: {
        textAlign: 'center',
        fontSize: 14,
        // color: 'rgba(0, 0, 0, .7)',
        opacity: .7,
        position: 'fixed',
        left: 0,
        right: 0,
        bottom: 0
    },
    loadPercent: {
        background: '#000',
        color: '#fff',
        fontWeight: 500
    },
    directionIco: {
        display: 'flex',
        position: 'fixed',
        top: 0,
        bottom: 0,
        fontSize: 30,
        opacity: .3,
        width: 20,
        // background: 'rgba(0, 0, 0, .1)',
        alignItems: 'center',
        justifyContent: 'center'
    },
    leftIco: {
        left: 0,
        background: 'linear-gradient(to right, #999, transparent)'
    },
    rightIco: {
        right: 0,
        background: 'linear-gradient(to left, #999, transparent)'
    },
    scrollView: {
        overflow: 'auto',
        bottom: 20,
        textAlign: 'justify'
    }
});

const Read = props => {
    // console.log(props)
    const { settings } = props
    const [story, setStory] = useState('')
    const [pages, setPages] = useState([])
    const [pageNo, setPageNo] = useState(0)
    const [isLoading, setLoading] = useState(true)
    const [loadStatus, setLoadStatus] = useState(0)

    let { id, name } = props.match.params
    name = decodeURIComponent(name)
    const classes = useStyles()
    useEffect(() => {
        leavingPage = false

        fetch(URL_READ_STORY + id)
        .then(r => r.text())
        .then(r => {
            
            if (settings.readerPref === 'PAGE') {
                // Prepend all \n chars with a space
                r = r.replace(/\n/g, '\n ')
                createPages(r)
            }
            else {
                setStory(r)
            }
        })
        .catch(err => console.log(err))

        return () => leavingPage = true
    }, [])

    /**
     * This function will break the text into string array, where each string will
     * hold the string that can be contained in 1 page of current view
     * @param {*} txt 
     */
    const createPages = txt => {
        setLoading(true)
        let words = txt.split(' ')
        let i = 0
        let pages = []
        let container = document.getElementById('hiddenhelper')
        container.style.fontSize = settings.fontSize + 'px'
        
        innerCalc(i)
        function innerCalc (i) {
            if(leavingPage) return

            //ALL DONE, Final setState for all the pages
            if(i > words.length) {
                console.log( (new Date()).getTime() )
                setPages(pages)
                setLoading(false)
                return
            }

            let wordCapacity = getPageCapacity(words.slice(i))
            let page = words.slice(i, i + wordCapacity).join(' ')
            if(page.trim()) pages.push(page)
            i += wordCapacity
            let loadingStatus = Math.floor((i/words.length)*100)
            // console.log('completed ' + loadingStatus + '%')

            // IF the current / required page is ready
            if( (pageNo + 1) === pages.length ) {
                console.log( (new Date()).getTime() )
                setPages([...pages])
                // Give setTimeout a green signal
            }
            setLoadStatus(loadingStatus)

            // Call itself asyncronously, so that setStates can run in between
            setTimeout(()=>{
                innerCalc(i)
            }, 0)
        }
    }

    /**
     * Given some words (array), it will return the number of words from that page that
     * can be fit in one page
     * @param {*} txt 
     */
    const getPageCapacity = words => {
        let pageContent = ''
        let wordCapacity = Math.floor(window.innerHeight/10)    // word capacity per page, init with an 'atleast' assumption
        let pageHeight = readableHeight // some extra blank space
        let container = document.getElementById('hiddenhelper')

        do {
            wordCapacity += 1
            pageContent = words.slice(0, wordCapacity).join(' ')
            container.innerText = pageContent
        }
        while(
            wordCapacity < words.length 
            && container.offsetHeight < pageHeight
        )
        
        return (wordCapacity - 1)
    }

    const getStyle = () => ({
        fontSize: settings.fontSize
    })

    return (
        <Box>
            <div className={classes.storyTitle}>{name}</div>
            {/* 
                A hidden div to put random texts and check if it is growing to fit
                the current window size
            */}
            
            {/* PAGE BY PAGE RENDERING SECTION */}
            <div className={(settings.readerPref === 'PAGE') ? '' : 'hidden'}>
                <Box className={clsx(classes.body, 'readerpage')} style={getStyle()}>
                    {pages[pageNo]}
                </Box>
                <div className={clsx(classes.pageCount, isLoading && 'hidden', 'night-contrast')}>
                    {pageNo + 1} of {pages.length}
                </div>

                <div className={clsx(!isLoading && 'hidden', classes.pageCount, classes.loadPercent, 'night-contrast')}>
                    <span>loading ({loadStatus}%)</span>
                </div>

                <span className={clsx(isLoading && 'hidden')}>
                    <span 
                        onClick={() => setPageNo(pageNo-1)}
                        className={clsx(classes.directionIco, classes.leftIco, (pageNo === 0) && 'hidden')}
                    >
                        <LeftIcon />
                    </span>
                    
                    <span 
                        onClick={() => setPageNo(pageNo+1)}
                        className={clsx(classes.directionIco, classes.rightIco, (pageNo === (pages.length-1)) && 'hidden')}
                    >
                        <RightIcon />
                    </span>
                </span>
            </div>
        
            {/* SCROLL VIEW SECTION */}
            <div className={(settings.readerPref === 'SCROLL') ? '' : 'hidden'}>
                <div className={clsx('readerpage', classes.scrollView)}>{story}</div>
            </div>
        </Box>
    )
}
const mapStateToProps = state => ({
    settings: state.settings
})
export default connect(mapStateToProps)(Read)