import {
    Menu,
    About,
    Read,
    Settings
} from './pages'
import LANG from '../utils/lang'

const routes = [
    {
        path: '/menu',
        component: Menu,
        displayName: LANG.MENU
    },
    {
        path: '/about',
        component: About,
        displayName: LANG.ABOUT
    },
    {
        path: '/read/:id/:name',
        component: Read
    },
    {
        path: '/settings',
        component: Settings,
        displayName: LANG.SETTINGS
    }
]

export default routes