import {
    SETTINGS_SAVE
} from '../actionTypes'

const initSettings = {
    isNightMode: false,
    fontSize: 16,
    readerPref: 'SCROLL'  // 'PAGE' or 'SCROLL'
}

const settingsReducer = (state=initSettings, action) => {
    switch(action.type) {
        case SETTINGS_SAVE: {
            return action.payload
        }

        default:
            return state
    }
}

export default settingsReducer